.card {
    border: solid 1px #e8e8e8;
    background-color: $white;

    &-header,
    &-body,
    &-footer {
        padding: 30px;
    }

    .card-header-img {
        background-repeat: no-repeat;
        background-size: cover;
        overflow: hidden;
        position: relative;


        .card-text-overlap {
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            color: $white;

            a {
                color: $white;
            }
        }
    }

    &-header {
        background-color: $deep-cyan-blue;
        min-height: 88px;

        &-title {
            float: left;
            color: $white;
        }

        .btn {
            float: right;
            color: $white;
        }

        .btn-link {
            padding-left: 0;
            padding-right: 0;
        }
    }

    &-body {
        min-height: 200px;
        background: $white;
    }

    &-footer {
        background: $light-gray;
        min-height: 44px;
    }

    // Asiapedia
    &.asiapedia,
    &.events {
        min-height: 568px;
    }

    &.asiapedia {
        .card-header-img {
            background: url(../img/banner-asiapedia.jpg);
        }

        .card-body {
            text-align: center;
        }
    }

    // Events
    &.events {
        .card-header-img {
            background: url(../img/das-events-banner.jpg);
        }
    }

    // Contact US
    &.contact-us {
        background: url(https://www.dezshira.com/themes/dsa/images/topics-banners/RightContact-us_Background.jpg) no-repeat top left;
        width: auto;
        height: 420px;
        background-size: cover;
    }

    // International Desks
    &.international-desks {
        min-height: 615px;

        .card-body {
            padding: 20px;
        }
    }

    // Services
    &.services {
        min-height: 430px;
        position: relative;
    }
}