.text-right{ text-align: right;}
.text-center{ text-align: center;}
.block-center{ margin-left: auto; margin-right: auto;}

.section-title {
	border-top: 3px solid $deep-blue;
	border-bottom: 1px solid $soft-bluish-gray;
	line-height: 20px;
	font-size: 14px;
	text-transform: uppercase;
	padding: 12px 18px;
	color: $bright-gray;
	font-weight: 400;
	margin-bottom: 0px;
	margin-bottom: 0px;
	
}
.text-white { color: $white !important; 
a{ color: $white;}
}
.transparent-bg{background:  rgba(0, 0, 0, 0.5);  width: 100%;}
.center-block{ margin: 0 auto; display: block;}
.centered {	position: absolute;	top: 50%;	left: 50%;	transform: translate(-50%, -50%);  }
.shadow{  @include box-shadow(0 0px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19));}
.img-circle{@include border-radius(50%);	border: $border-default;}
.zoom-on-hover img {@include transition(transform .5s ease); width: 100%; display: block; }
.zoom-on-hover:hover img { @include transform(scale(1.2)); }
.down-arrow{border: solid #9599a0; position: relative;margin-left: 5px;top: -3px;
	border-width: 0 1px 1px 0;
	display: inline-block;
	padding: 4px;
	transform: rotate(45deg);
	@include transform(rotate(#{45deg}deg));
}